  max-width: 80%;
  overflow-x: scroll;
  margin: 0 auto;
  height: 26px;
  margin-top: 10px;
  padding: 7px;

} */

.message-body-pre{
  padding: 0.25rem;
}

.address-avatar {
  top: 10px;
  position: relative;
}
.message-address {
  font-weight: 400;
  font-size: 18px;
}

.message-box-value {
  margin-top: 5px;
  font-weight: bold;
}

.indent-1 {
  margin-left: 340px;
}
.indent-2 {
  margin-left: 540px;
}


.tx-table-inner-container {
  display: flex;
  flex-wrap: wrap;
}

.tx-details-box {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border-top: 1px solid #aaa;  
  border-bottom: 1px solid #aaa;
  padding: 10px;

}

.fee-row {
  display: inline-flex;
  flex-direction: row;
  word-wrap: break-word;
  white-space: normal;
  align-items: flex-end;
  font-size: 12px;
}

.fee-value{
  
  margin-left: 10px;
  min-width: 40px;
}

